import React from 'react';


class VisionImage extends React.Component {
  state = {
    documentLoaded: false,
    status: false,
  }

  componentDidMount() {
    const { onLoadStart, onLoad } = this;
    const { onIntersect } = this.props;

    window.addEventListener('load', () => {
      window.VISION3K.onIntersect = onIntersect;

      this.setState({ documentLoaded: true }, () => {
        window.VISION3K.observe(this.elem, {
          onLoadStart,
          onLoad,
        });
      });
    });
  }

  onLoadStart = () => {
    this.setState({ status: 'loading' }, () => this.props.onVision);
  }

  onLoad = () => {
    this.setState({ status: 'loaded' }, () => this.props.onLoad);
  }

  _concatClassNames = (arr) => {
    return arr.filter(Boolean).join(' ');
  }

  render() {
    const { status } = this.state;
    const {
      classNames = {},
      containerStyle = {},
      imageStyle = {},
      src,
    } = this.props;
    const initialStyle = {
      container: containerStyle.initial,
      image: imageStyle.initial,
    };
    const loadingStyle = {
      container: containerStyle.loading,
      image: imageStyle.loading,
    };
    const loadedStyle = {
      container: containerStyle.loaded,
      image: imageStyle.loaded,
    };
    const loadingClassNames = {
      container: classNames.containerLoading,
      image: classNames.imageLoading,
    };
    const loadedClassNames = {
      container: classNames.containerLoaded,
      image: classNames.imageLoaded,
    };
    const styles = status === 'loading' ? loadingStyle : loadedStyle;
    const classes = status === 'loading' ? loadingClassNames : loadedClassNames;

    return (
      <figure
        ref={elem => this.elem = elem}
        style={{
          ...initialStyle.container,
          ...styles.container,
        }}
        className={this._concatClassNames([classNames.containerBase, status && classes.container])}>
        <img
          data-src={src}
          src="data:image/gif;base64,R0lGODlhAQABAAD/ACwAAAAAAQABAAACADs="
          style={{
            ...initialStyle.image,
            ...styles.image,
          }}
          alt="VISION3KSAMPLE"
          className={this._concatClassNames([classNames.imageBase, status && classes.image])} />
      </figure>
    )
  }
}


export default VisionImage;


