import React, { Component } from 'react';
import styled, { css, keyframes } from 'react-emotion';
import VisionImage from './VisionImage';
import AnchorLink from 'react-anchor-link-smooth-scroll';


const IMAGES = [
  'https://images.collection.cooperhewitt.org/18907_c8619685c9af9b3f_o.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/c/c4/Cerambycidae_-_Dorcacerus_barbatus.jpg',
  'https://images.collection.cooperhewitt.org/3523_4ea78776db5605bc_b.jpg',
  'https://media.vam.ac.uk/media/thira/collection_images/2006AA/2006AA4341.jpg',
  'https://images.collection.cooperhewitt.org/181485_b748a40d55f23d4c_b.jpg',
  'https://images.collection.cooperhewitt.org/61628_9f59621770e3c33e_x.jpg',
  'https://images.collection.cooperhewitt.org/61337_9aba4b71d1a69805_b.jpg',
  'https://images.collection.cooperhewitt.org/90459_fffbbc85300d1530_b.jpg',
  'https://images.collection.cooperhewitt.org/53995_c7a83d90e1a7e16a_x.jpg',
  'https://images.collection.cooperhewitt.org/65610_d26d06dd31c8fd8c_b.jpg',
  'https://images.collection.cooperhewitt.org/82336_33dc4f377f098de8_b.jpg',
  'https://images.collection.cooperhewitt.org/90438_d3492b28e79eb7b7_x.jpg',
  'https://lh3.googleusercontent.com/H2YHL1UHXEDmA5O9GjUABMvvgXEfnWSuEZw1Ro84aRaY1B5fegxcrepZfQw2WAIPIvNoHtqpjY4h6EAyZp6e0VRqyAc=s0',
  'https://images.collection.cooperhewitt.org/182324_a212b44c7eb32c5d_b.jpg',
  'https://media.vam.ac.uk/media/thira/collection_images/2006BH/2006BH7964.jpg',
  'https://lh3.googleusercontent.com/ohfKfi9aPgVDmhfIRlTPTyiYFTZ8jm6N9SGFMHOvBTGDHRRXDgZA6F2FnioBT0W3yD4x-yA8cFTh5dMdOWc96T59n22X=s0',
  'https://images.collection.cooperhewitt.org/51068_6e56e44554204285_b.jpg',
  'https://images.collection.cooperhewitt.org/262750_bb51e977a2385cce_b.jpg',
  'https://images.collection.cooperhewitt.org/180509_5dc12bcaec4e2137_b.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/0/0b/AgrihanNASA.jpg',
  'https://nrs.harvard.edu/urn-3:HUAM:LEG001920_dynmc',
  'https://images.collection.cooperhewitt.org/45311_88fafb1773232e08_b.jpg',
];

class App extends Component {
  state = {
    loadState: false,
    halted: true,
    viewed: [],
    inView: [],
  }

  shouldComponentUpdate(nextProps, nextState) {
    const { halted, viewed, inView } = this.state;
    return !Object.is(nextState.viewed, viewed) || !Object.is(nextState.inView, inView) || nextState.halted !== halted;
  }

  onIntersect = (inView, viewed) => {
    this.setState({
      inView,
      viewed,
    });
  }

  onVision = () => {
    this.setState({ loadState: 'loading' });
  }

  onLoad = (elem, img, viewed) => {
    this.setState({
      loadState: 'loaded',
      viewed,
    });
  }

  _sortJoin = arr => arr.sort((a, b) => a - b).join(', ');

  render() {
    return (
      <main>
        <Intro>
          <h1>(React)Vision3000</h1>
          <p>
            OK. so this is an exploration but it works and usable, i think (WIP).<br/>
            <br/>
            So why da hell rebrand Lazyload?<br/>
            Why not? and well, let's try pushing a bit!<br/>
            <br/>
            Here's the thing, lazyloading still relies on scroll events + boundingRect(),
            nothing wrong with that, that works but IntersectionObserver is out and is good (have a <a href="https://developer.mozilla.org/en-US/docs/Web/API/Intersection_Observer_API">read on MDN</a> and Google's <a href="https://developers.google.com/web/fundamentals/performance/lazy-loading-guidance/images-and-video/">guide</a> on lazyloading assets with a mention of it).<br/>
            <br/>
            So this is an attempt to <strong>explore</strong> "how to only load images that a user intends to view" and use the Modern IntersectionObserver.<br/>
            <br/>
            Some background: IntersectionObservers, lazyloading (waypoints) are only concerned when a target crosses a threshold. Once it does, it runs a callback. So for example, if you want to go from Image A &rarr; Image G, with current implementations it will load everyhing from Image B to F regardless if you scroll fast or whatever. Just as long as you "pass by" any target you will load that whether or not you intend to view it (read anchor links, or just scrolling really fast on an image heavy page).<br/>
            <strong>Now with Vision3k, think of the viewport as a target area, once it stops, it shoots then loads!</strong><br/>
            <br/>
            So, see that list to the left, those are anchor links. Try clicking on one e.g. go from 0 to 19. You can see that it will not load nothing else, only 19 or something else that peeks into the viewport, because that's what you intended to view. Also, try scrolling real fast. you'll see that it'll only put into VISION what you want. So there you go, a quick experiment! Stil currently cleaning up things, but this is already available as a standalone JS module and as a React Component. Soon!
          </p>
        </Intro>

        <Nav>
          <ul>
            { IMAGES.map((src, index) => {
              const isInView = this.state.inView.includes(index + 1);
              const isViewed = this.state.viewed.includes(index + 1);

              return (
                <li key={`list-${index}`}>
                  <AnchorLink
                    className={isViewed ? styles.viewed : ''}
                    href={`#image-${index}`}>
                    { isViewed ? '' : '' } #{ index }

                    <span>
                      { isInView && '  '}
                      { (isInView && !isViewed) && ' Loading...'}
                      { isViewed && '   Loaded!'}
                    </span>
                  </AnchorLink>
                </li>
              )
            }) }
          </ul>
        </Nav>

        { IMAGES.map((src, index) => (
          <Wrap key={index} id={`image-${index}`}>
            <VisionImage
              src={src}
              classNames={{
                containerBase: styles.container,
                imageBase: styles.image,
                containerLoading: styles.containerLoading,
                containerLoaded: styles.containerLoaded,
                imageLoaded: styles.imageLoaded,
              }}
              onIntersect={this.onIntersect}
              onLoadStart={this.onVision}
              onLoad={this.onLoad} />
          </Wrap>
        ))}
      </main>
    );
  }
}

const fadeIn = keyframes`
  0% { opacity: 0; }
  100% { opacity: 1; }
`;

const Intro = styled('div')`
  position: fixed;
  right: 15px;
  top: 15px;
  width: 300px;
  z-index: 9999;
  font-family: 'Arial';

  h1 {
    margin: 0 0 15px 0;
  }

  p {
    font-size: 12px;
  }
`;

const Wrap = styled('div')`
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;

  &:nth-child(even) {
    background-color: #cdcdcd;
  }
`;

const Nav = styled('nav')`
  position: fixed;
  top: 0;
  left: 0;
  font-size: 18px;
  font-family: 'Arial';
  font-weight: bold;

  ul {
    list-style: none;
  }

  a {
    color: blue;
    text-decoration: none;
  }

  span {
    color: hotpink;
  }
`;

const styles = {
  container: css`
    width: 40vw;
    height: auto;
    background: blue;
    box-sizing: border-box;
    position: relative;
    margin: 0;
  `,
  containerLoading: css`
    background: cyan;
  `,
  containerLoaded: css`
    background: green;
  `,
  image: css`
    width: 100%;
    height: auto;
    display: block;
    opacity: 0;
  `,
  imageLoaded: css`
    animation: ${fadeIn} 0.5s 0.1s forwards;
  `,
  viewed: css`
    color: green;
  `,
}

export default App;



